  directives: {   //
    trigger:{
      inserted(el, binging){
      //  console.log("inserted:"+el.id);
        if(el.id==='ballBtn'){
          el.click();
        }

      }
    },
    drag(el) {
      let oDiv = el;  // 
      let firstTime = "", lastTime = "";  //
      document.onselectstart = function () {       // 
        return false;
      }
      oDiv.onmousedown = function (e) {
        let disX = e.clientX - oDiv.offsetLeft          //     //      // 
        let disY = e.clientY - oDiv.offsetTop
        document.getElementById("dragbtn").setAttribute("data-flag", false);
        document.getElementById("dragbtn").setAttribute("show2", oDiv.offsetLeft);
        firstTime = new Date().getTime();
        document.onmousemove = function (e) {
          let l = e.clientX - disX               //    
          let t = e.clientY - disY
          // console.log(e.clientX+"   ")
          if (l <= 0) {
            l = 0;
          } else if (l >= document.documentElement.clientWidth - oDiv.clientWidth) {
            //document.documentElement.clientWidth 
            l = document.documentElement.clientWidth - oDiv.clientWidth
          }
          document.getElementById("dragbtn").setAttribute("show2", l.toString()); //this
          if (t <= 0) {
            t = 0;
          } else if (t >= document.documentElement.clientHeight - oDiv.clientHeight) {  //  oDiv.clientHeight
            t = document.documentElement.clientHeight - oDiv.clientHeight              // document.documentElement.clientHeight 
          }
          oDiv.style.left = l + 'px'             // 
          oDiv.style.top = t + 'px'
        }
        document.onmouseup = function (e) {
          document.onmousemove = null;
          document.onmouseup = null;
          lastTime = new Date().getTime();
          if ((lastTime - firstTime) < 200) {
            document.getElementById("dragbtn").setAttribute("data-flag", true)
          }
        }
        return false          // return falsedivonmouseup
      }
    }
  },

