  /**  */
    queryValveCurrentData (){
      let queryCompany=this.companyIds;
      let queryChild=this.childCompanyIds;
      let queryInstitute=this.instituteIds;
      let queryStation=this.stationIds;
      if (this.queryOrgType > this.checkOrgTreeType) {
        this.$alert('', '!', {
          confirmButtonText: '',
          callback: action => {
          }
        });
        return;
      }

      let that = this;
      let _jsondata_ =   {
        perPageSize:this.queryParam.pageSize,
        currentPage:this.queryParam.currentPage,
        cui_login_id:this.platUserSearchForm.cui_login_id,
        cui_realname:this.platUserSearchForm.cui_realname,
        cui_wx_nick_name:this.platUserSearchForm.cui_wx_nick_name,
        cui_concat_phone:this.platUserSearchForm.cui_concat_phone,
        cui_status:this.platUserSearchForm.cui_status,
        check_org_id:this.checkOrgTreeId,
        check_org_type:this.checkOrgTreeType,
        is_check_child:this.isCheckChild,
        login_name:this.loginName,
        cui_type:this.customGlobal.userInfoExt.cui_type,
        is_check:this.isCheck,
        query_company:queryCompany,
        query_child:queryChild,
        query_institute:queryInstitute,
        query_station:queryStation
      } ;
      if(this.sortOrderCol!==''){
        _jsondata_.orderby = this.sortOrderCol;
      }else{
        _jsondata_.orderby =' cui_writetime desc ';
      }
    //  console.info(JSON.stringify(_jsondata_));
      const loading = this.$loading({
        lock: true,
        text: '...',
        target: '.main',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, .7)'
      });

      //4post
      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.thermalAccount_query,// 
        this.customGlobal.serverUrl.orgBaseURI,//
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
      );
      //post
      p1.then((res) => {
        // console.log(":"+JSON.stringify(res));
        if(res.data.status=='success'){

          loading.close();
          that.tableData = res.data.rows;
          that.queryParam.totalRow = res.data.total;
        }else{
         // console.info("");
          loading.close();
          that.tableData = [];
          this.$alert(res.data.msg, '', {
            confirmButtonText: '',
            callback: action => {
            //  console.info('data:'+JSON.stringify(res.data));
              if(res.data.msg==='' && res.data.status==='failed'){
                this.$router.replace({path:'/login'});
              }
            }
          });
        }
      },error=>{
      //  console.log(":"+JSON.stringify(error));
        loading.close();
        that.tableData = [];
        this.$alert(JSON.stringify(error.message), '!', {
          confirmButtonText: '',
          callback: action => {

          }
        });
      }).catch((res) => {
      //  console.log(":"+JSON.stringify(res));
        loading.close();
        that.tableData = [];
        this.$alert(JSON.stringify(res.message), '', {
          confirmButtonText: '',
          callback: action => {

          }
        });
      });

    },

