    noCheckTreeTable(rows){//
      this.tableRowIdMap=new Map();
      for (let i = 0; i < rows.length; i++) {
        this.$refs.thermalRoleTreeTable.toggleRowSelection(rows[i],false)
        rows[i].status = false;
        if(rows[i].detail){
          this.noCheckTreeTable(rows[i].detail);
        }
      }
    },

    /***
     * 
     * @param curId ID
     * @param sourceData tableData
     * @param targetData 
     * @param layer 12
     * @param flag 123,45statustrue
     *
     */
    checkPermissionId(curId,sourceData,targetData,layer,flag){
     // console.info("checkPermissionId:"+sourceData)
     // console.info("ID:"+curId);
      var result = false;
      for(var i1=0;i1<sourceData.length;i1++){
      //  console.info("id:"+sourceData[i1].id);
        if(flag==='1'){
          if(sourceData[i1].id===curId ){
            if(layer==='1'){//
              sourceData.push(targetData);
              this.tableDataClone = this.tableData;
            //  console.info("add find ok");
              result = true;
              break;
            }else if (layer==='2'){
              if(sourceData[i1].hasOwnProperty("detail")){
               // console.info("add detail,:"+JSON.stringify(sourceData[i1]))
                let detail = sourceData[i1].detail;

                detail.push(targetData);
                sourceData[i1].detail = detail;
                this.tableDataClone = this.tableData;
                result = true;
                break;
              }else{
                let detail = [];

                detail.push(targetData);
                sourceData[i1].detail = detail ;
              //  console.info("add detail,detail:"+JSON.stringify(sourceData[i1].detail));
                this.tableDataClone = this.tableData;
                result = true;
                break;
              }
            }
            //break;
          } else{
            if(sourceData[i1].hasOwnProperty("detail")){
            //  console.info("add not find detail")
              result = this.checkPermissionId(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
            //  console.info("add not find");
              result = false;
            }
          }

        }//end if(flag==='1'){
        else  if(flag==='2'){
          if(sourceData[i1].id===curId ){
            sourceData[i1].caption = targetData.caption;
            sourceData[i1].vue = targetData.vue;
            sourceData[i1].url = targetData.url;
            sourceData[i1].icon = targetData.icon;
          //  console.info("modify find ok");
            this.tableDataClone = this.tableData;
            result = true;
            break;
          }else{
            if(sourceData[i1].hasOwnProperty("detail")){
            //  console.info("modify not find detail")
              result = this.checkPermissionId(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
            //  console.info("modify not find");
              result = false;
            }
          }
        }else if(flag==='3'){
          if(sourceData[i1].id===curId ){
            sourceData.splice(i1, 1);
            this.tableDataClone = this.tableData;
          //  console.info("del find ok");
            result = true;
            break;
          }else{
            if(sourceData[i1].hasOwnProperty("detail")){
            //  console.info("del not find detail")
              result = this.checkPermissionId(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
            //  console.info("del not find");
              result = false;
            }
          }
        }else if(flag==='4'){
        //  console.info('');
          //if(sourceData[i1].caption.toLowerCase().includes(curId.toLowerCase())){
       //   console.info("query find indexOf:"+sourceData[i1].caption.toLowerCase().indexOf(curId.toLowerCase()));
          if(sourceData[i1].caption.toLowerCase().indexOf(curId.toLowerCase())>-1){
            let newdata = [];
            newdata.push(sourceData[i1]);
            this.tableDataClone = newdata;
          //  console.info("query find ok."+ sourceData[i1].caption);
            result = true;
            break;
          }else{
            if(sourceData[i1].hasOwnProperty("detail")){
            //  console.info("query not find detail")
              result = this.checkPermissionId(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
            //  console.info("query not find");
              this.tableDataClone = [];
              result = false;

            }
          }
        }else if(flag==='5'){
         // console.info('status');
          //if(sourceData[i1].caption.toLowerCase().includes(curId.toLowerCase())){
        //  console.info("query find status id:"+sourceData[i1].id);
          if(sourceData[i1].id===curId){
            sourceData[i1].status = 'true';
         //   console.info("modify status find ok");
            this.tableDataClone = this.tableData;
          //  console.info("query find status ok id:"+ sourceData[i1].id);
            result = true;
            break;
          }else{
            if(sourceData[i1].hasOwnProperty("detail")){
          //    console.info("query status not find detail")
              result = this.checkPermissionId(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
          //    console.info("query status not find");
              this.tableDataClone = [];
              result = false;

            }
          }
        }


      }
      return result;

    },

  /**  ()*/
    checkedTreeTable(roleIds){
     // console.log("---Tree----"+roleIds)
      this.$nextTick(() => {
        this.tableTreeData.forEach(outerItem => {
          // 
          if (outerItem.detail != '') {
            outerItem.detail.forEach(outerItem1 => {
              //
              for (let i = 0; i < roleIds.length; i++) {
                if (outerItem1.id === roleIds[i]) {
                  this.$refs.thermalRoleTreeTable.toggleRowSelection(outerItem1, true);
                  //  console.log(outerItem1.caption + "----" + outerItem1.caption)
                }
              }
              if (outerItem1.detail != '') {
                outerItem1.detail.forEach(outerItem2 => {
                  //treeTable
                  for (let i = 0; i < roleIds.length; i++) {
                    if (outerItem2.id === roleIds[i]) {
                      this.$refs.thermalRoleTreeTable.toggleRowSelection(outerItem2, true);
                      //  console.log(outerItem1.caption + "----" + outerItem2.caption)
                    }
                  }
                })
              }
            })
          }
        })
      })
    },

  /** tableData */
    initTableDataPermissionStatus(permissionKey,tableData){
      for (let k of Object.keys(permissionKey)) {
        this.checkPermissionId(k,tableData,
          {},
          '0',
          '5');
      }
      //
      this.$nextTick(() => {
        this.initLoopCheckTable(tableData);
      })
    },

